#login-form-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
} 

/* #root { 
    display: block;
} */

.form-container {
    background-color: #fff;
    width: 95%;
    padding: 30px;
    border-radius: 15px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 1);
}

.form-container h1 {
    margin-top: 20px;
    font-weight: 700;
    color: #05668d;
    text-align: center;
}

.form .form-group {
    height: 40px;
    width: 100%;
    position: relative;
    margin-top: 40px;
}


.form .form-group button {
    height: 100%;
    width: 100%;
    background-color: #4DB6AC;
    font-size: 16px;
    border: none;
    border: 2px solid #05668d;
}

.form .form-group button:hover {
    height: 100%;
    width: 100%;
    background-color: #4bb325;
    font-size: 16px;
    font-weight: 700;
    border: none;
    border: 2px solid #0b9ad3;
}

.form .form-group input {
    height: 100%;
    width: 100%;
    border: none;
    border-bottom: 2px solid silver;
    outline: none;
}

.form-group input:focus ~ label,
.form-group input:valid ~ label {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
    font-size: 14px;
    outline: none;
}

.form .form-group label {
    position: absolute;
    bottom: 20px;
    left: 0;
    color: #05668d;
    pointer-events: none;
    transition: all 0.6s ease;
}

.form .form-group .underline {
    position: absolute;
    bottom: 0px;
    height: 2px;
    width: 100%;
} 

.form .form-group .underline:before {
    position: absolute;
    content: "";
    bottom: 0px;
    height: 100%;
    width: 100%;
    background: #4DB6AC;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition: -webkit-transform 0.6s ease;
    transition: transform 0.6s ease;
    transition: transform 0.6s ease, -webkit-transform 0.6s ease;
} 

.form-group input:focus ~ .underline:before,
.form-group input:valid ~ .underline:before {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}

.login-icon{
    display: flex;
